.footer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: $grey-46;
    padding: 15px 30px;

    @include breakpoint(medium) {
        justify-content: flex-end;
    }

    &__copy-right-text {
        font-size: 12px;
        color: $grey-f0;
    }
}
